.center_nav{
    display: flex;
    margin-left: 13%;
    margin-right:13% ;
    top: 20rem;
    border-bottom: 2px solid #E0E0E0;
    border-top: 2px solid #E0E0E0;
    margin-top: 1.25rem;
    align-items: center;
    justify-content: space-between;
    background: #FFFFFF;

    
}

.list{
    display: flex;
    list-style: none;
    align-items: center;
    justify-content:left;
    text-decoration: none;
    
}

.center_nav>.list> li{
    padding: .7rem 1.3rem;
     position: relative;
     font-size: 1.2rem;
     font-weight: 400;
}

.center_nav>.list> li > a{
    text-decoration: none;
    color: #8a8a8a;
    font-size: 1.2rem;
    position: relative;
    font-weight: 400;
}

.center_nav>.list> li:hover:after{
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    border-bottom: 2px solid black;
    width: 100%;
    
}
.center_nav>.list> li>a:hover{
    color: black;
}

.center_nav>.right{
    display: flex;
    align-items: center;
    justify-content: right;
}

.center_nav>.right>.post_btn{
    display: flex;
    align-items: center;
    background: #edeef0;
    height: 2.1rem;
    font-size: 1rem;
    padding: 0.1rem .5rem;
    margin-right: 1.3rem;

}

.center_nav>.right>.join_desktop{
    background: #2f6ce5;
    padding: 0.3rem .5rem;
    min-width: 15%;
    font-size: 1rem;
    color: #fff;
    font-weight: 500;
    margin-right:1.3rem;
}
.center_nav>.right>.join_desktop>img{
    height: .8rem;
}
.join_mobile{
    display: none;
}

.postadd_mobile{
    display: none;
}


/* main center */
.main_center{
    display:grid;
    grid-template-columns: 1fr .7fr;
    /* border: 1px solid black; */
    margin-left: 13%;
    margin-right:13% ;
    margin-top: 1.25rem;
    position: relative;
}

.post{
    border: 2px solid #e0e0e0;
    border-radius: .5rem;
    margin-bottom: 1rem;
}

.post_image{
    height: auto;
    width: 100%;
    
}

.left_container >.post> .post_content{
    /* border: 1px solid red; */
    margin: 10px 15px;
}
/* .left_container >.post> .post_content>img{
    height: 1.3rem;
} */

.post_heading{
    display: flex;
    font-size: 1.1rem;
    align-items: center;
    justify-content: space-between;
    position: relative;
    margin-top: 1rem;
    margin-bottom: 1rem;
    
}

.left_container >.post> .post_content>.hand {
    content: "\270D";
    /* height: 1.3rem; */
    display: flex;
    text-align: center;
    align-items: center;
    width: 4.8125rem;
    height: 1.5625rem;
    font-size: 1.125rem;
    align-self: center;
  }
  .left_container >.post> .post_content>.hand>h4{
      text-align: center;
      font-size: 1.125rem;
      padding-left: .5rem;
      padding-top: .4rem;
      
  }

.post_heading>h2{
    font-weight: 600;
    font-size: 1.375rem;
    margin-right: 6%;
}

.post_heading>button{
    position: absolute;
    top: .1rem;
    right: 0;
    background: none;
    border: none;
    cursor: pointer;
}

.post_heading>button>.fas.fa-ellipsis-h{
    width: 1.5rem;
    /* font-size: 1rem; */
}


.left_container>.post>.post_content>h4{
