.signup_page{
    position: sticky;
   
   transform: none;
   top: 0%;
   bottom: 0%;
   left: 0;
    z-index: 150;
    background: #fff;
    width: 100%;
    height: 32.0625rem;
   display: block;
   padding: 1rem;
   border-bottom-left-radius: 0;
   border-bottom-right-radius: 0;
 }


 .signupwrapper{
    margin: 0;
  display: grid;
 grid-template-columns: 1fr ;
 align-items: center;
 justify-content: center;
 background: #fff;
 width: 100%;
}

.signupright>img{
   display: none;
}

.signupheader{
    display: none;
   
     
}

.create_account{
    background: #2F6CE5;
    border-radius: 1.25rem;
    color: white;
    border: none;
    font-size: 0.875rem;
    height: 2.5rem;
    flex: 0.55;
}
.create_accountmobile{
    display:block;
    flex: 0.45;
   text-align: right;
   padding-right: .5rem;
   text-decoration: underline;
   font-weight: 500;
   font-size: .8125rem;
}

.signupright>h4{
    display: none;
}

.signupright>h5{
    text-align: center;

}

.hide_page_btn{
    position: absolute;
     right: 2rem;
     top:2rem ;
     font-size: 1.5rem;
     z-index: 500;
     width: 2rem;
     height: 2rem;
     border-radius: 2rem;
     border: none;
     font-weight:bold ;
     background: #212529;
     display: flex;
     text-align: center;
     align-items: center;
     justify-content: center;
     cursor: pointer;
     color: #fff;
     padding-bottom: .2rem;
     
 }



